import Swal from "sweetalert2"
import { GET_SHOPIFY_DATA } from "../reducer/keys"
import { SHOPIFY_DELETE_PUBLISH, SHOPIFY_GET_PUBLISH, SHOPIFY_PUBLISH } from "./api"
import HttpClient from "./http-client"

export const GET_SHOPIFY_PUBLISH_DATA = () => {
    const obj = new FormData()
    obj.append('wallId', localStorage.getItem('active_wall'))
    return (dispatch) => {
        new HttpClient().post(SHOPIFY_GET_PUBLISH, obj).then(res => {
            const { status, data } = res.data;
            dispatch({
                type: GET_SHOPIFY_DATA,
                payload: data,
            });
        }).catch((error) => {
            console.log("ERROR", error)
        });
    }
}

export const DELETE_SHOPIFY_PUBLISHED = (id) => {
    return (dispatch) => {
        new HttpClient().post(SHOPIFY_DELETE_PUBLISH, {id})
            .then((response) => {
                const { status, data } = response.data;
                if (status) {
                    dispatch(GET_SHOPIFY_PUBLISH_DATA());
                    Swal.fire({
                        title: "Deleted!",
                        icon: "success",
                        timer: 2000,
                        showCancelButton: false,
                        showConfirmButton: false
                    })
                }
            }).catch((error) => {
                console.log("ERROR", error)
            });
    }
}

export const PUBLISH_SHOPIFY_GALLERY = (obj, callback =() =>{}) => {
    return (dispatch) => {
        new HttpClient().post(SHOPIFY_PUBLISH, obj)
            .then((response) => {
                console.log(response.data)
                const { status } = response.data;
                if (status) {
                    dispatch(GET_SHOPIFY_PUBLISH_DATA());
                    Swal.fire({
                        title: "Publish successfully",
                        icon: "success",
                        timer: 2000,
                        showCancelButton: false,
                        showConfirmButton: false
                    })
                    callback()
                }

            }).catch((error) => {
                console.log("ERROR", error)

            });
    }
}