import { useState } from 'react';
import moment from 'moment';
import Select from 'react-select';

const AutomaticPublish = ({ ShopifyData, themeoption, showSection, themepageoption, galleryoptions, positionoption, publishGallery, onChangePages,onThemeChange,onSectionChange, deleteGallery }) => {
    const [showPublishing, setShowPublishing] = useState(!(ShopifyData && ShopifyData.embedData && ShopifyData.embedData.length > 0))
    const [loading, setLoading] = useState(false)
    
    const onPublished = () => {
        setLoading(false)
        setShowPublishing(false)
    }

    const onPublish = () => {
        setLoading(true)
        publishGallery(onPublished)
    }
    return (
        <>
            {!showPublishing ?
                <div className='afterPublish_screen'>
                    <div className="d-flex align-items-center justify-content-between manage_galleries">
                        <div className="publish_content">
                            <h4>Manage Published Galleries</h4>
                            <p>Please Note: Manually installed plugins will not appear in this list</p>
                        </div>
                        <button className='btn btn-primary' onClick={() => setShowPublishing(true)}>Add Automatic Publishing</button>
                    </div>
                    <div className='galleries_table'>
                        <div className="table-responsive">
                            <table className="table table-bodered">
                                <thead>
                                    <tr>
                                        <th>Page</th>
                                        <th>Position</th>
                                        <th>Date Installed</th>
                                    </tr>
                                </thead>
                                <tbody>

                                    {ShopifyData && ShopifyData.embedData && ShopifyData.embedData.length ? ShopifyData.embedData.map((item, i) => {
                                        return <tr key={i}>
                                            <td>{item.pageTitle}</td>
                                            <td>{item.position}</td>
                                            <td className="text-nowrap">{moment(item.createdAt).format('MMMM DD, YYYY, hh:mm:ss A')} </td>
                                            <td className="text-right">
                                                <a href={`//${item.domain}`} target="_blank" className="btn btn-link btn-icon btn-clean mx-1 shopifyEmbedPreview">
                                                    <span className="svg-icon-col svg-icon-2x">
                                                        <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24">
                                                            <g id="Group_4540" data-name="Group 4540" transform="translate(9376 -4253)">
                                                                <rect id="Rectangle_2094" data-name="Rectangle 2094" width="24" height="24" transform="translate(-9376 4253)" fill="none" opacity="0"></rect>
                                                                <g id="Group_4497" data-name="Group 4497" transform="translate(-9374 4258)">
                                                                    <path id="Path_5232" data-name="Path 5232" d="M21.87,11.5c-.64-1.11-4.16-6.68-10.14-6.5C6.2,5.14,3,10,2.13,11.5a1,1,0,0,0,0,1c.63,1.09,4,6.5,9.89,6.5h.25c5.53-.14,8.74-5,9.6-6.5A1,1,0,0,0,21.87,11.5ZM12.22,17c-4.31.1-7.12-3.59-8-5,1-1.61,3.61-4.9,7.61-5,4.29-.11,7.11,3.59,8,5-1.03,1.61-3.61,4.9-7.61,5Z" transform="translate(-1.996 -4.996)" fill="#545454"></path>
                                                                    <path id="Path_5233" data-name="Path 5233" d="M12,8.5A3.5,3.5,0,1,0,15.5,12,3.5,3.5,0,0,0,12,8.5Z" transform="translate(-1.996 -4.996)" fill="#545454"></path>
                                                                </g>
                                                            </g>
                                                        </svg>
                                                    </span>
                                                </a>
                                                <button type="button" data-id="4222" data-message="Delete" className="btn btn-link btn-icon btn-clean shopifyEmbedDelete mx-1" onClick={() => deleteGallery(item.id)}>
                                                    <span className="svg-icon-col svg-icon-2x">
                                                        <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24">
                                                            <g id="Group_4611" data-name="Group 4611" transform="translate(10956 -4533)">
                                                                <rect id="Rectangle_2069" data-name="Rectangle 2069" width="24" height="24" transform="translate(-10956 4533)" fill="none" opacity="0"></rect>
                                                                <g id="Group_4324" data-name="Group 4324" transform="translate(-10952 4536)">
                                                                    <path id="Path_5188" data-name="Path 5188" d="M2.722,16.328A2,2,0,0,0,4.694,18h6.612a2,2,0,0,0,1.972-1.672L14,6H2ZM9,9.5A.5.5,0,0,1,9.5,9h1a.5.5,0,0,1,.5.5v5a.5.5,0,0,1-.5.5h-1a.5.5,0,0,1-.5-.5Zm-4,0A.5.5,0,0,1,5.5,9h1a.5.5,0,0,1,.5.5v5a.5.5,0,0,1-.5.5h-1a.5.5,0,0,1-.5-.5ZM15,2H11A2,2,0,0,0,9,0H7A2,2,0,0,0,5,2H1A1,1,0,0,0,1,4H15a1,1,0,0,0,0-2Z" fill="#545454" fillRule="evenodd">
                                                                    </path>
                                                                </g>
                                                            </g>
                                                        </svg>
                                                    </span>
                                                </button>
                                            </td>
                                        </tr>
                                    })
                                        : ""}
                                </tbody>
                            </table>
                        </div>
                    </div>
                </div>
                :
                <div className='autopublish_section'>
                    <div className="autoPublish_heading">
                        <h4>Automatic Publishing</h4>
                        <p>Just select your shopify page, theme, position of gallery, and then click on Publish Gallery.</p>
                    </div>
                    <div className='autoPublish_selectOptions'>
                        <div className='form-group'>
                            <div className='row'>
                                <div className='col-xxl-6 col-lg-6 col-md-6 col-sm-12'>
                                    <div className='form-group'>
                                        <label className='text-dark font-weight-bold'>
                                            Select Your Shopify Theme
                                        </label>
                                       {themeoption&&themeoption.length ? <Select options={themeoption} defaultValue={themeoption[0]} onChange={onThemeChange} /> : 
                                       <select name="" className="form-control kt-selectpicker change-theme"><option value="" disabled="">Select Theme</option></select>
                                       }
                                    </div>
                                </div>
                                <div className='col-xxl-6 col-lg-6 col-md-6 col-sm-12'>
                                    <div className='form-group'>
                                        <label className='text-dark font-weight-bold'>
                                            Select Your Shopify Landing Page
                                        </label>
                                        {galleryoptions && galleryoptions.length ? <Select options={galleryoptions} defaultValue={galleryoptions[0]} onChange={onChangePages} /> :
                                        <select name="" className="form-control kt-selectpicker change-theme"><option value="" disabled="">Select Page</option></select>}
                                    </div>
                                </div>
                                {showSection ?
                                    <div className='col-xxl-6 col-lg-6 col-md-6 col-sm-12 dawn-theme-sections'>
                                        <div className='form-group'>
                                            <label className='text-dark font-weight-bold'>Select Dawn Theme Home Page Section</label>
                                            {themepageoption && themepageoption.length ? <Select options={themepageoption} defaultValue={themepageoption[0]} onChange={onSectionChange} /> : 
                                            <select name="" className="form-control kt-selectpicker change-theme"><option value="" disabled="">Select Page</option></select>}
                                        </div>
                                    </div> : ""}
                                <div className='col-xxl-6 col-lg-6 col-md-6 col-sm-12'>
                                    <div className='form-group'>
                                        <label className='' htmlFor='position-bottom'>
                                            Position of Gallery on Page
                                        </label>
                                        <Select options={positionoption} defaultValue={positionoption[0]} />
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className='d-flex align-items-center justify-content-end mb-5 autoPublish_buttons'>
                        {ShopifyData && ShopifyData.embedData && ShopifyData.embedData.length ? <button className='btn btn-outline-secondary me-3' onClick={() => setShowPublishing(false)}>Cancel</button> : <></>}
                        <button className={`btn btn-primary ${loading ? 'spinner spinner-white disabled spinner-right' : ''}`} onClick={onPublish}>Publish Gallery</button>
                    </div>
                </div>
            }
        </>
    )
}

export default AutomaticPublish