import React, { Component } from 'react'
import ReactTooltip from 'react-tooltip';
import { EMBED_CODE, EMBED_URL } from '../../../../actions/api'; 

export default class homeEmbed extends Component {
    state = { width: 100, height: 100, widthPr: '%', heightPr: '%', copied: false }
    copyText = (e) => {
        var copyText = document.getElementById('wallembedareaLiteCode03').value
        const textarea = document.createElement('textarea');
        textarea.value = copyText;
        textarea.style.visibility = 'none';
        document.body.appendChild(textarea);
        textarea.select();
        document.execCommand('copy');
        document.body.removeChild(textarea);
        this.setState({ copied: true })
        setTimeout(() => { this.setState({ copied: false }) }, 1000)

    }
    render() {
        const { width, height, widthPr, heightPr, copied } = this.state 
        const { activeWebsite } = this.props
        return (<>
            <div className='pl-lg-5 pr-lg-5'>
                <div className='form-group mb-5'>
                    <h3 className='mb-1 font-weight-boldest'>Manual Publishing</h3>
                    <p>Copy the below-mentioned “PUBLISH CODE” and paste it into the HTML of your Shopify store’s landing page where you want to show the gallery.</p>
                    
                </div>
                <div className='form-group heightWidth_section'>
                    <div className='row'>
                        <div className="col-sm-12">
                            <p>This option gives you the freedom to publish gallery anywhere on a webpage.</p>
                        </div>
                        <div className='col-xxl-6 col-lg-12 col-md-6 col-sm-12'>
                            <div className='form-group mb-2'>
                                <div className='input-group'>
                                    <div className='input-group-prepend'>
                                        <span className='input-group-text'>Width</span>
                                    </div>
                                    <input type="number" id="getWidth" className="form-control form-control-lg getWidth" max={widthPr == '%' ? "100" : ''} min={0} value={width} onChange={(e) => this.setState({ width: e.target.value })} />
                                    <div className='input-group-append'>
                                        <select className="kt-selectpicker form-control form-control-lg" id="embed-width02" onChange={(e) => this.setState({ widthPr: e.target.value })}>
                                            <option value="%" selected={widthPr == '%' ? true : false} >%</option>
                                            <option value="px" selected={widthPr == 'px' ? true : false} >px</option>
                                        </select>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="col-xxl-6 col-lg-12 col-md-6 col-sm-12">
                            <div className="form-group mb-2">
                                <div className="input-group">
                                    <div className="input-group-prepend">
                                        <span className="input-group-text">Height</span>
                                    </div>
                                    <input type="number" id="getHeight02" className="form-control form-control-lg getHeight" value={height} max={heightPr == '%' ? "100" : ''} min={0} onChange={(e) => this.setState({ height: e.target.value })} />
                                    <div className="input-group-append">
                                        <select className="kt-selectpicker form-control form-control-lg" id="embed-height02" onChange={(e) => this.setState({ heightPr: e.target.value })}>
                                            <option value="%" selected={heightPr == '%' ? true : false} >%</option>
                                            <option value="px" selected={heightPr == 'px' ? true : false} >px</option>
                                        </select>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

                <div className="form-group">
                    <div className="position-relative mb-0 embed_code embed_code_box light_embcode_box">
                        <div className="tab-content" id="myTabContent">
                            <div className="tab-pane fade embed_code show active" id="nav-without-iframe-shopify" role="tabpanel" aria-labelledby="nav-without-iframe-tab-shopify">
                                <textarea readOnly="readonly" id="wallembedareaLiteCode1" className="form-control form-control-solid copyme" style={{ height: '2.22222px' }} onClick={(e) => this.copyText(e)}
                                    value={(`<div class="tagshop" style="width:${width}${widthPr};height:${height}${heightPr};overflow: auto;"><div class="tagshop-socialwall" data-wall-id="${localStorage.getItem('website_id')}" data-source="website" view-url="${EMBED_CODE}/${activeWebsite ? activeWebsite : localStorage.getItem('website_id')}"></div><script src="${EMBED_URL}" type="text/javascript"></script></div>`)}></textarea>
                                {copied ? <span id="copy-success-alert" style={{ display: "block" }}>Copied!</span> : ''}
                                <div className="emebed_action">
                                    <button className="btn btn-text p-0 mr-2 copy-btn" data-toggle="tooltip" data-theme="dark" title="Copy Code" onClick={(e) => this.copyText(e)}>
                                        <svg xmlns="http://www.w3.org/2000/svg" width="18.17" height="18.17" viewBox="0 0 18.17 18.17">
                                            <g id="Group_7224" data-name="Group 7224" transform="translate(-32 -32)">
                                                <path id="Path_7006" data-name="Path 7006" d="M124.005,112H114.92a2.92,2.92,0,0,0-2.92,2.92v9.085a2.92,2.92,0,0,0,2.92,2.92h9.085a2.92,2.92,0,0,0,2.92-2.92V114.92A2.92,2.92,0,0,0,124.005,112Z" transform="translate(-76.755 -80)" fill="#c7c7c7"></path>
                                                <path id="Path_7007" data-name="Path 7007" d="M37.191,44.811h9.567a2.925,2.925,0,0,1-2.753,1.947H34.92A2.92,2.92,0,0,1,32,43.838V34.753A2.925,2.925,0,0,1,33.947,32v9.567A3.245,3.245,0,0,0,37.191,44.811Z" transform="translate(0 3.412)" fill="#c7c7c7"></path>
                                            </g>
                                        </svg> 
                                    </button>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

            </div>


        </>
        )
    }
}
