import React, { Component } from 'react'
import ShopifyPublish from './elements/ShopifyPublish'
import OtherPlatforms from './elements/OtherPlatforms'
import { connect } from "react-redux";
import { GET_SHOPIFY_ACCOUNT } from '../../../actions/productAction';
import { GET_SHOPIFY_PUBLISH_DATA } from '../../../actions/shopifyAction';
import './shopify-publish.css'

 class index extends Component {
     componentDidMount(){
         this.props.GET_SHOPIFY_PUBLISH_DATA()
     }
     componentDidUpdate(prevProps) {
        if(prevProps.activeWebsite !== this.props.activeWebsite){
            this.props.GET_SHOPIFY_PUBLISH_DATA()
        }
     }
  render() {
    return <ShopifyPublish/>
  }
}


const mapStateToProps = (state) => { 
    return {
        ShopifyData: state.ShopifyData.ShopifyData,
        activeWebsite: state.websiteList.activeWebsite_id,
    }
}
const mapDispatchToProps = dispatch => {
    return {
        GET_SHOPIFY_PUBLISH_DATA: () => dispatch(GET_SHOPIFY_PUBLISH_DATA()),

    }
}
export default connect(mapStateToProps, mapDispatchToProps)(index);